import { useEffect, useState } from "react";
import { supabase } from "../lib/supabaseClient";

const Categorias = () => {
  const [openDropdown, setOpenDropdown] = useState<string | null>(null);
  const [categories, setCategories] = useState<
    Array<{ name: string; id: number }>
  >([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchCategories() {
      try {
        console.log("Intentando obtener categorías...");

        // Usar exactamente la sintaxis recomendada por Supabase
        let { data: categorias, error } = await supabase
          .from("categorias")
          .select("id_categoria,nombre_categoria");

        console.log("Respuesta de Supabase:", { categorias, error });

        if (error) {
          console.error("Error al obtener categorías:", error);
          setError(`Error: ${error.message}`);
          throw error;
        }

        if (!categorias || categorias.length === 0) {
          console.log("No se encontraron categorías");
          setError("No se encontraron categorías en la base de datos");
          return;
        }

        // Transformar los datos al formato requerido
        const formattedCategories = categorias.map((category) => ({
          id: category.id_categoria,
          name: category.nombre_categoria,
        }));

        console.log("Categorías formateadas:", formattedCategories);
        setCategories(formattedCategories);
      } catch (error) {
        console.error("Error en la operación:", error);
        setError(
          "Error al cargar las categorías. Revisa la consola para más detalles."
        );
      } finally {
        setLoading(false);
      }
    }

    fetchCategories();
  }, []);

  const handleDropdownToggle = (category: string) => {
    if (openDropdown === category) {
      setOpenDropdown(null);
    } else {
      setOpenDropdown(category);
    }
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center py-4">
        Cargando categorías...
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex justify-center items-center py-4 text-red-600">
        {error}
      </div>
    );
  }

  if (categories.length === 0) {
    return (
      <div className="flex justify-center items-center py-4">
        No se encontraron categorías
      </div>
    );
  }

  return (
    <div className="flex justify-center items-center gap-15 text-lg pt-4 pb-4">
      <a
        href="#mejores-ofertas"
        className="cursor-pointer hover:text-red-600 transition-colors mx-4"
      >
        Ofertas
      </a>
      {categories.map((category) => (
        <div key={category.id} className="relative mx-4">
          <button
            onClick={() => handleDropdownToggle(category.name)}
            className="flex items-center hover:text-red-600 transition-colors"
          >
            {category.name} {/* Solo se muestra el nombre, no el ID */}
            <svg
              className={`ml-1 w-4 h-4 transition-transform ${
                openDropdown === category.name ? "rotate-180" : ""
              }`}
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M19 9l-7 7-7-7"
              ></path>
            </svg>
          </button>
          {openDropdown === category.name && (
            <div className="absolute z-50 mt-2 w-64 bg-white rounded-md shadow-lg py-1 text-base max-h-96 overflow-y-auto left-0">
              <span className="block px-4 py-2 text-gray-500">
                Categoría: {category.name}
              </span>
            </div>
          )}
        </div>
      ))}
    </div>
  );
};

export default Categorias;
